.nav-bar{
	width: 70%;
}

.menu {
	display: flex;
	justify-content: space-between;
	width: 100%;
	max-width: 300px;

	@media (max-width: 900px){
		max-width: unset;
		flex-direction: column;
		align-items: center;
	}

	li {
		padding: 0 10px;

		@media (max-width: 900px){
			padding: 30px 10px;
		}
	}
}
